import 'antd/dist/antd.css';
import './App.css';
import './components/style.css'
import { Table } from 'antd'

function App() {

  const data = [
    {
      name:'Geethanjali',
      age:20,
      emailid:'geethanjaligorle2001@gmail.com',
      key:'1'
    },
    {
      name:'Varshini',
      age:21,
      emailid:'varshinigarapati54@gmail.com',
      key:'2'
    },
    {
      name:'Jahnavi',
      age:22,
      emailid:'jahnavigedela21@gmail.com',
      key:'3'
    },
  ]
  
  const columns = [
    {
      title:'Name',
      dataIndex:'name',
      key:'key',
      render: name =>{
        return <a>{name}</a>
      }
    },
    {
      title:'Age',
      dataIndex:'age',
      key:'key',
      sorter:(a,b) => a.age - b.age
      
    },
    {
      title:'EmailId',
      dataIndex:'emailid',
      key:'key'
    },
    {
      title:'Graduated?',
      key:'key',
      render: payload =>{
        return <p>{payload.age>20?'True':'False'}</p>
      }
    }
  ]

  return (
    <div className="App">
      <header className="App-header">
       
     <Table
     dataSource={data}
     columns={columns}
     
     >

     </Table>
      </header>
    </div>
  );
}

export default App;
